<svelte:options tag="my-qrcode" immutable="{true}"/>
<script lang="ts">
  import { onMount } from 'svelte';
  import { qrCanvas } from './qrcode';

  export let text: string;
  export let size: number = 400;
  let container: Element;

  onMount(() => {
    const qr = qrCanvas(text, size);
    container.appendChild(qr);
  });
</script>

<template lang="pug">
div(bind:this="{container}")
</template>
