import QR from 'qr.js';
export function qrCanvas(text, size = 200) {
    const qrcode = QR(text);
    const canvas = document.createElement('canvas');
    canvas.width = size;
    canvas.height = size;
    const ctx = canvas.getContext('2d');
    const cells = qrcode.modules;
    const tileSize = size / cells.length;
    for (let r = 0; r < cells.length; r += 1) {
        const row = cells[r];
        for (let c = 0; c < row.length; c += 1) {
            ctx.fillStyle = row[c] ? '#000' : '#fff';
            const w = (Math.ceil((c + 1) * tileSize) - Math.floor(c * tileSize));
            const h = (Math.ceil((r + 1) * tileSize) - Math.floor(r * tileSize));
            ctx.fillRect(Math.round(c * tileSize), Math.round(r * tileSize), w, h);
        }
    }
    return canvas;
}
